}

div#hero {
  width: 100vw;
  height: 100vh;
  color: red;
  padding-top: 100px;
  font-size: 80px;
}

div#hero video {
  width: 100vw;
  height: 100vh;
  animation: smooth-in .3s ease-out;
  position: absolute;
  top: 0;
  left: 0;
}

div#hero video.out {
  opacity: 0;
  animation: smooth-out .3s ease-out;
}

@keyframes smooth-in {
  0% {
    opacity: 0;
    transform: translateX(-100vw)scale(1);
  }

  100% {
    opacity: 1;
    transform: translateX(0)scale(1);
  }
}

@keyframes smooth-out {
  0% {
    opacity: 1;
    transform: translateX(0)scale(1);
  }

  99% {
    opacity: 0;
    transform: translateX(100vw)scale(.2);
  }

  100% {
    opacity: 0;
    transform: translateX(100vw)scale(.2);
  }
}

